import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import {RequestActivePlan} from '../../../../domain/models/plans/requestactiveplan.model';

@Injectable()
export class ActivePlansService implements IRepository<RequestActivePlan> {

  constructor(private http: HttpClient) {
  }

  public create(values : RequestActivePlan): Observable<RootObject> {
    var response = this.http.post<RootObject>(`https://default-bepartners-dev.apps.appcanvas.net/servicebroker/v1/plans/pc/activepayplan/`,values);
    return response;
  }
}
